
.c_xibao {
    margin-top: 20px;
    padding-left: 10px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    font-family: "Microsoft YaHei";
}
.c_xibao img {
    width: 110px;
    height: 80px;
}
.c_yixue{
    width: 50%;
  margin-left: 10px;
}
.c_yixue p{
    margin: 0px;
    color: #323232;
}
.c_name{
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    margin-top: 40px;
}
.c_names{
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    font-size: 10px;
    color: #8e8e8e;
    width:40%;
}
.c_names img{
    width:15px;
    height: 15px;
}



