
.a_back {
    background-color: #29ac3a;
    width: 100%;
    height: 300px;
    padding: 1px;
    font-family: "Microsoft YaHei";
}
.a_top {
    width: 80%;
    height: 100%;
    float: left;
}
.a_top span {
    display: block;
    width: 70%;
    height: 100%;
    color: #fff;
    font-size: 22px;
    text-align: center;
    line-height: 60px;
}
.a_return img {
    width: 20%;
    margin-top: 10px;
    margin-left: 13%;
}
.a_return {
    width: 20%;
    float: left;
}
.a_yue {
    width: 100%;
    text-align: center;
}
.a_yue p{
    color: #fff;
}
.a_kuai{
    width: 110px;
    height: 45px;
    border-radius:30px;
    background-color: #fff;
    line-height: 45px;
    font-size: 18px;
    color: #1ca52f;
}



