
.g_boss {
    font-family: "Microsoft YaHei";
}
.g_back {
    width: 100%;
    height: auto;
    z-index: 0;
}
.g_back img {
    width: 100%;
    height: 300px;
}
.g_head {
    width: 100%;
    text-align: center;
    position: fixed;
    top: 100px;
}
.g_head img {
    width: 120px;
    height: 120px;
}
.g_font {
    width: 100%;
    position: fixed;
    top: 220px;
    line-height: 30px;
}
.g_font p {
    margin: 0px;
    text-align: center;
}
.g_list {
    width: 100%;
    line-height: 50px;
    height: 40px;
}
.g_list div {
    display: inline-block;
}
.g_item {
    width: 100%;
    border-bottom: 1px solid #ccc;
}
.gc_img {
    padding-left: 20px;
}
.gc_img img {
    width: 25px;
    height: 25px;
}
.nav {
    position: fixed;
    bottom: 0px;
    background-color: #efefef;
    width: 100%;
    height: auto;
}
.nav_list {
    margin: 0px;
    padding: 0px;
}
.nav_item {
    width: 18.5%;
    text-align: center;
    font-size: 14px;
    font-family: "SimHei";
    display: inline-block;
}
.nav_img {
    width: 50%;
    margin-left: 22%;
}
.nav_img img {
    width: 70%;
}



