
.r_weix {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      border-bottom: 1px solid #ccc;
}
.r_zhifu {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
}
.r_one {
      width: 20%;
      text-align: center;
}
.r_one img {
      width: 50%;
      margin-top: 20px;
}
.r_two {
      width: 20%;
      text-align: center;
}
.r_two img {
      width: 60%;
      margin-top: 20px;
}
.r_fontz {
      width: 60%;
      font-size: 20px;
      font-family: "Microsoft YaHei";
}
.r_fontz p {
      margin-top: 27px;
}
.r_cert{
      border-top: 10px solid #f4f4f4;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      line-height: 50px;
      border-bottom: 1px solid #ccc;
}
.r_acc{
      width: 30%;
      font-size:18px;
       font-family: "Microsoft YaHei";
}
.r_input input{
  width: 70%;
  -webkit-appearance: none;
  /*去掉input的默认样式*/
  -moz-appearance: none;
  border:none;
  height: 2.3em;
  outline: 0;
  /*去掉input的轮廓*/
  text-align: center;
  background-color: transparent;
  /*设置input的背景颜色为透明*/
  border-radius: 4px;
  font-size: 1.1em;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
}
input:focus:-moz-placeholder {
  color: transparent;
}
/* Mozilla Firefox 19+ */
input:focus::-moz-placeholder {
  color: transparent;
}
/* Internet Explorer 10+ */
input:focus:-ms-input-placeholder {
  color: transparent;
}
input::-moz-placeholder {
  color: #727170;
}
input::-webkit-input-placeholder {
  color: #727170;
}
.r_quren{
    width: 80%;
    background-color: #ff900c;
    height: 30px;
    color: #fff;
    text-align: center;
    line-height: 30px;
    margin-left: 5%;
    margin-top: 30px;
    border-radius: 15px;
}



