
.q_list {
    border-bottom: 1px solid #e8e8e8;
}
.q_item {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    margin-top: 15px;
    margin-bottom: 10px;
}
.q_img {
    width: 30%;
}
.q_wid {
    width: 80%;
    margin-left: 15px;
}
.q_img img {
    width: auto;
    height: auto;
    max-height: 100%;
    max-width: 100%;
}
.q_right {
    width: 50%;
}
.l_list {
    margin: 0px;
    padding-left: 10px;
}
.l_list p {
    margin: 0px;
    line-height: 1.7em;
    font-family: "Microsoft YaHei";
}
.l_list li:nth-child(1) {
    font-size: 4vmin;
}
.l_list  li:nth-child(2) {
    font-size: 1vmin;
    color: #717171;
}
.l_list li:nth-child(3) {
    font-size: 1vmin;
    color: #717171;
}



