
.f_para {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 40px;
    text-align: right;
    font-size: 2.5vmax;
    font-family: "Microsoft YaHei";
}
.f_para p {
    margin: 0px;
}
.f_para img {
    width: 10px;
    height: 5px;
    margin-bottom: 10%;
    margin-left: 5px;
}
.f_para div {
    line-height: 40px;
}
.f_p {
    width: 60%;
}
.f_list {
    border-top: 5px solid #f4f4f4;
    font-family: "Microsoft YaHei";
    font-size: 1rem;
}
.f_item {
    border-bottom: 1px solid #ebebeb;
}
.f_item div {
    display: inline-block;
}
.f_right {
    width: 78%;
    height: 32px;
    padding-left: 10px;
}
.f_right p {
    margin: 0px;
    height: inherit;
}
.f_left {
    width: 15%;
    text-align: right;
}
.f_left img {
    width: 15px;
    height: 20px;
}



