
.o_half {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
}
.o_half div {
    width: 50%;
    text-align: center;
    line-height: 50px;
    border-bottom: 1px solid #ccc;
}
.o_hal {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
}
.o_hal div {
    width: 50%;
    text-align: center;
    line-height: 50px;
    border-bottom: 1px solid #ccc;
}
.o_red {
    color: #f00;
}
.o_book {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    margin-top: 20px;
    padding-left: 10px;
    line-height: 20px;
}
.o_book img {
    width: 100px;
    height: 80px;
}
.expand-transition {
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    height: 30px;
    padding: 10px;
    background-color: #eee;
    overflow: hidden;
}
/* .expand-enter 定义进入的开始状态 */
/* .expand-leave 定义离开的结束状态 */
.expand-enter,
.expand-leave {
    height: 0;
    padding: 0 10px;
    opacity: 0;
}



